.card{
	min-width: 250px;
	min-height: 400px;
	width: 14vw;
	border:1px solid black;
	-webkit-box-shadow: 2px 4px rgba(0, 0, 0, 0.4);
	        box-shadow: 2px 4px rgba(0, 0, 0, 0.4);
	display: -ms-flexbox;
	display: flex;
	-ms-flex-direction: row;
	    flex-direction: row;
	-ms-flex-pack: center;
	    justify-content: center;
	-ms-flex-wrap: wrap;
	    flex-wrap: wrap;
	border-radius: 20px;
	-webkit-transition: all 300ms;
	-o-transition: all 300ms;
	transition: all 300ms;
	cursor: pointer;
	margin: 10px;
	background-color: rgba(200,200,200,0.3);
}

.card:hover{
	-webkit-transform: scale(1.02);
	    -ms-transform: scale(1.02);
	        transform: scale(1.02);
}

.card-title{
	display: -ms-flexbox;
	display: flex;
	-ms-flex-pack: center;
	    justify-content: center;
	-ms-flex-align: center;
	    align-items: center;
	-ms-flex-item-align: start;
	    align-self: flex-start;
	height: 15%;
	width: 100%;
	background-color: rgb(23, 26, 45);
	margin: 0px;
	padding: 10px 0px;
	color: white;
	font-weight: 700;
	font-size: 250%;
	border-top-left-radius: 20px;
	border-top-right-radius: 20px;
}

.card-content{

}

.card-clicker{
	min-width: 250px;
	min-height: 400px;
	width: 14vw;
	border-radius: 20px;
	z-index: 1;
	opacity: 1;
	position: absolute;
}


