.modal-window{
	position: fixed;
	display: -ms-flexbox;
	display: flex;
	-ms-flex-pack: center;
	    justify-content: center;
	-ms-flex-align: center;
	    align-items: center;
	top:0px;
	left:0px;
	bottom: 0px;
	width: 100%;
	height: 100%;
	z-index: 10;
	background-color: rgba(25, 25, 25, 0.8);

}

.close-button{
	position: fixed;
	display: -ms-flexbox;
	display: flex;
	-ms-flex-align: center;
	    align-items: center;
	-ms-flex-pack: center;
	    justify-content: center;
	top:10px;
	right:10px;
	height: 50px;
	width: 50px;
	background-color: white;
	border-radius: 10px;
	font-size: 250%;
	font-weight: 700;
	cursor: pointer;
	background-color: rgba(255,255,255,0.6);
	color: rgb(23, 26, 45);
	-webkit-transition: all 500ms;
	-o-transition: all 500ms;
	transition: all 500ms;
}


.close-button:hover{
	background-color: rgba(255,255,255,1);
	cursor: pointer;
}

.modal{
	background-color: white;
	height: 40vh;
	min-height: 400px;
	width: 30vw;
	min-width: 300px;
	border-radius: 10px;
}


